:root {
    --logo-width : 300px;
    --logo-height : 51px;
  }
  
  .videoChatting{
    height:100vh;
    overflow: hidden;
    background-color: black;
    display: flex;
    flex-direction: column;
    padding : 0px 10px 0px 10px;
  }

  .videoChatting-header{
    display: flex;
    justify-content : space-between;
  }

  .videoChatting-header-text{
    margin-top: 20px;
    margin-left:20px;
    font-size: 28px;
    font-weight: 600;
    height: 100%;
    color:#ffffff;
  }
  .videoChatting-header-quit{
    margin-top: 25px;
    display: flex;
    height: 100%;
  }

  .videoChatting-header-quit-icon{
    width: 20px;
    height: 20px;
    color : white;
  }

  .videoChatting-header-quit-text{
    height: 100%;
    font-size: 18px;
    color:white;
  }

  .videoChatting-header-mic-onoff,
  .videoChatting-header-camera-onoff, 
  .videoChatting-header-share-onoff{
    width: 100%;
    height: 100%;
    margin-top: 5px;
    display: flex;
    justify-content: space-around;
  }
  .videoChatting-header-mic-on, .videoChatting-header-mic-off, 
  .videoChatting-header-camera-on, .videoChatting-header-camera-off,
  .videoChatting-header-share-on, .videoChatting-header-share-off{
    height: 24px;
    width: 24px;
    z-index: 3;
  }
  .videoChatting-header-mic-off{
    margin-top: 2px;
  }

  .videoChatting-header-mic-Activie, 
  .videoChatting-header-camera-Activie, 
  .videoChatting-header-share-Activie{
    position: relative; /* 절대 위치를 사용하기 위해 상대 위치 설정 */
    margin-top: 20px;
    border-radius: 71px;
    background-color: #d9d9d9;
    height: 50%;
    width: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }

  .videoChatting-header-mic, 
  .videoChatting-header-camera, 
  .videoChatting-header-share{
    position: relative; /* 절대 위치를 사용하기 위해 상대 위치 설정 */
    margin-top: 20px;
    border-radius: 71px;
    background: linear-gradient(180deg, rgb(197.68, 43.11, 47.2) 0%, rgb(233.69, 157.52, 159.54) 100%);
    height: 50%;
    width: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  
  .slider-mic, .slider-camera, .slider-share{
    position: absolute;
    height: 32px;
    width: 32px;
    background-color: rgb(68, 64, 64);
    border-radius: 50%; /* 원형으로 만듭니다. */
    transition:0.3s;
    transform: translateX(-50%); /* 가로 중앙 정렬 */
    top: 9%; /* 세로 중앙 정렬 */
    left : 25%;
    cursor: pointer; /* 클릭 가능함을 나타냅니다. */
    z-index: 2;
  }
  .slider-mic-Activie, .slider-camera-Activie, .slider-share-Activie{
    position: absolute;
    height: 32px;
    width: 32px;
    background-color: rgb(68, 64, 64);
    border-radius: 50%; /* 원형으로 만듭니다. */
    transition:0.3s;
    transform: translateX(-50%); /* 가로 중앙 정렬 */
    top: 9%; /* 세로 중앙 정렬 */
    left : 75%;
    cursor: pointer; /* 클릭 가능함을 나타냅니다. */
    z-index: 2;
  }
  

  .videoChatting-header-login-info{
    height: 100%;
    display: flex;
    margin-top: 5px;
  }

  .videoChatting-header-login-info-text{
    font-size: 15px;
    font-weight: 600;
    height: 100%;
    color:white;
    margin-right: 10px;
  }

  .videoChatting-body{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    height: calc(100% - 75px);
  }

  /* left 
  1. local video group
  2. local video size control
  3. remote video group
  

  right 
  text chatting toggle
  text chatting group
  */

  .videoChatting-group-video{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 75%;
    height: 100%;
    transition:0.3s;
    position: relative;
    left : 0;
    top : 0;
  }

  .videoChatting-group-video-Chattingclose{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 98%;
    height: 100%;
    position: relative;
  }

/* local start*/

  .videoChatting-localVideo-groups{
    width: 100%;
    height: 77.8%;
    display: flex;
    flex-direction: column;
    position: relative;
    top : 0;
    left : 0;
  }

  .videoChatting-localVideo{
    height: 100%;
    width: 100%;
    background-color: rgb(148, 148, 148);
    position: relative;
    left: 0;
    top : 0;
  } 

  .videoChatting-localVideo-sizeControl{
    display: flex;
    flex-direction: row-reverse;
    align-content: flex-end;
    flex-wrap: wrap;
    width: 100%;
    height: 70px;
    background: linear-gradient(to top, black, transparent);
    position: absolute;
    bottom: 0;
    z-index: 1;
  }

  .videoChatting-localVideo-sizeControl img{
    height: 28px;
    width: 28px;
  }

  
  /*local remoteclose part start*/
  .videoChatting-localVideo-groups-remoteclose{
    width: 100%;
    height: 97%;
    display: flex;
    flex-direction: column;
    position: relative;
    transition:0.3s;
  }

  .videoChatting-localVideo-remoteclose{
    height:100%;
    width: 100%;
    background-color: rgb(148, 148, 148);
    transition: 0.3s;
    position: relative;
    top:0;
    left:0;
  }

  .videoChatting-localVideo-sizeControl-remoteclose{
    display: flex;
    flex-direction: row-reverse;
    align-content: space-around;
    flex-wrap: wrap;
    width: 100%;
    height: 70px;
    background: linear-gradient(to top, black, transparent);
    position: absolute;
    bottom: 0;
    z-index: 1;
  }

  .videoChatting-localVideo-sizeControl-remoteclose img{
    height: 28px;
    width: 28px;
  }
  /*local remoteclose part finish*/
  

   /*local Chattingclose part start*/

   .videoChatting-localVideo-groups-Chattingclose{
    width: 100%;
    height: 77.8%;
    display: flex;
    flex-direction: column;
    position: relative;
    transition:0.3s;
    position: relative; 
    top: 0;
    left :0;
  }

  .videoChatting-localVideo-Chattingclose{
    position: relative;
    left : 0;
    top : 0;
    height: 100%;
    width: 100%;
    background-color: rgb(148, 148, 148);
  }

  .videoChatting-localVideo-sizeControl-Chattingclose{
    display: flex;
    flex-direction: row-reverse;
    align-content: space-around;
    flex-wrap: wrap;
    width: 100%;
    height: 70px;
    background: linear-gradient(to top, black, transparent);
    position: absolute;
    bottom: 0;
    z-index: 1;
  }

  .videoChatting-localVideo-sizeControl-Chattingclose img{
    height: 28px;
    width: 28px;
  }
  /*local Chattingclose part finish*/
/*All close start*/

.videoChatting-localVideo-groups-remoteclose-Chattingclose{
  width: 100%;
  height: 97%;
  display: flex;
  flex-direction: column;
  position: relative;
  
}

.videoChatting-localVideo-remoteclose-Chattingclose{
  height: 100%;
  width: 100%;
  background-color: rgb(148, 148, 148);
}

.videoChatting-localVideo-sizeControl-remoteclose-Chattingclose{
  display: flex;
  flex-direction: row-reverse;
  align-content: space-around;
  flex-wrap: wrap;
  width: 100%;
  height: 70px;
  background: linear-gradient(to top, black, transparent);
  position: absolute;
  bottom: 0;
  z-index: 1;
}

.videoChatting-localVideo-sizeControl-remoteclose-Chattingclose img{
  height: 28px;
  width: 28px;
}

/*All cloase finish*/
 
  /*local finish*/

  /*remote start*/

  .videoChatting-remoteVideo-group{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 22.2%;
    
  }
  .videoChatting-remoteVideo-group-remoteclose{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items : center;
    width: 100%;
    height: 3%;
    
  }
  .videoChatting-remoteVideo-group-Chattingclose{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items : center;
    width: 100%;
    height: 22.2%;
    
  }
  .videoChatting-remoteVideo-group-remoteclose-Chattingclose{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items : center;
    width: 100%;
    height: 3%;
    
  }

  .ArrowupUp{
    width: 135px;
  }
  .Arrowdown{
    width: 135px;
    margin-bottom: 5px;
  }
  .videoChatting-remoteVideo-group-remoteclose .Arrowupdown{
    width:135px;
  }

  .videoChatting-remoteVideo-group-videos{
    width: 100%;
    height: 160px;
    display: flex;
    justify-content: center;  
  }

  .videoChatting-remoteVideo-video-participant{
    height: 100%;
    width: 380px;
    margin-right: 20px;
    display: flex;
    flex-direction: column;
    position: relative;
  }

  .videoChatting-remoteVideo{
    height: 100%;
    width: 100%;
    margin-right: 20px;
    background-color: rgb(148, 148, 148);
  }

  .videoChatting-remoteVideo-participant{
    background: linear-gradient(to top, black, transparent);
    height: 20%;
    width: 100%;
    position: absolute;
    bottom: 0;
    z-index: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  .videoChatting-remoteVideo-participant-name{
    color: white;
  }

  .videoChatting-remoteVideo:last-child {
    margin-right : 0px;
  }

  /*remote finish*/

  /*textchatting start*/

  .videoChatting-textChatting-toggle{
    margin-top: 350px;
    width: 2%;
  }
  
  .videoChatting-textChatting-group{
    background-color: yellow;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 97%;
    width: 23%;
  }
  

