
.header-user {
  width: 260px;
  height: 70px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background-color: aqua;
  border-radius: 15px;
  box-shadow: 3px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.header-user-name {
  font-size: 20px;
  font-weight: 400;
  margin-right: 10px;
}
.header-user-icon {
  width: 60px;
  height: 60px;
  background-color: red;
  border-radius: 15px;
  border: 1px solid black;
  margin-right: 6px;
}