/* styles.css */
.chat-container {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.message {
  margin: 5px;
  padding: 10px;
  border-radius: 10px;
  max-width: 70%;
}

.my-message {
  align-self: flex-end;
  background-color: #FFE7AB;
  color: black;
}

.other-message {
  align-self: flex-start;
  background-color: #D9D9D9;
  color:black
}

.message-sender{
  font-weight: 600;
}

.videoChatting-textChatting-group-textChat{
  height: 100%;
  width: 100%;
  background-color: #fca7a7;
}

.videoChatting-textChatting-group-textChat-text{
  font-size: 24px;
  font-weight: 600;
}

.videoChatting-textChatting-group-textChat-chat{
  background-color: white;
  height: 95%;
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow-y: auto;
  overflow-x : hidden;
}

.videoChatting-textChatting-group-textChat-input{
  width: 95%;
  height: 60px;
  margin-left: 10px;
  display: flex;
  border: 2px solid;
  background-color: white;
  border-radius: 15px;
  justify-content: space-around;
  align-items: center;
  position: sticky;
  bottom: 0;
  z-index: 1;
}

.videoChatting-textChatting-group-textChat-input-typing{
  position: relative;
  min-width: 80%;
  max-width: 80%;
  min-height: 80%;
  max-height: 80%;
  border: none;
  resize:none;
  outline:none;
}

.videoChatting-textChatting-group-textChat-input-button{
  height: 90%;
  width: 7%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fca7a7;
  border:none;
  border-radius: 10px;
  cursor: pointer;
}

.sendicon{
  height: 20px;
  width: 20px;
}
